import React, { useEffect, useRef, useMemo } from 'react'
import "../style/Home.css"
import {gsap} from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'
import { Power3,Power1 } from 'gsap'
import { KeyboardArrowUp } from '@mui/icons-material'
import robolh from '../assets/robo-hand-left.png'
import roborh from '../assets/robo-hand-right.png'
import lightbulb from '../assets/cyborg-light-bulb-5.png'
import roboface from '../assets/cyborg-robot-face.png'

gsap.registerPlugin(ScrollTrigger);

function useSelectorHorizontal() {
  const ref = useRef();
  const q = useMemo(() => gsap.utils.selector(ref), [ref]);
  return [q, ref];
}

function useSelectorVertical() {
  const mainref = useRef();
  const qm = useMemo(() => gsap.utils.selector(mainref), [mainref]);
  return [qm, mainref];
}


const Home = () => {


  const [q, ref] = useSelectorHorizontal();
  const [qm, mainref] = useSelectorVertical();
  
  const containerOneTimeline = useRef();
  

  useEffect(()=>{
    
  const mainel = mainref.current
  
  
    gsap.to(q(".page"),{
      xPercent: -100*(q(".page").length-1),
      ease: "none",
      scrollTrigger:{
        trigger: ref.current,
        scrub:1,
        pin: true,
        // snap: 1/(q(".page").length - 1),
        // end: ()=> "+="+ `${ref.current.offsetWidth*2}`,
        end: "+=10000"
        // duration:30
      },
      
    })

    containerOneTimeline.current = gsap.timeline()
    .fromTo(
      mainel.querySelector(".container.first"),{
        opacity:0,
        y:-100,
      },{
          opacity:1,
          y:0,
          duration:3,
          ease: Power3.easeOut,   
          delay: 1
      })    
    .to(qm(".arrowup"),{
      y: "-40px",
      ease: Power3.easeOut,
      repeat: -1,
      yoyo: true,
      duration: 1,
    })
    
    gsap.to(qm(".content"),{
        x: "400px",
        duration: 3,
        scrollTrigger:{
          trigger: ".container.first",
          start: "top top",
          // end: "bottom top",
          toggleActions:"restart pause reverse pause",
          scrub: 1,
        },
        }
      )

    gsap.to(qm(".bulb"),{
        y: "-40px",
        ease: Power1.easeInOut,
        repeat: -1,
        yoyo: true,
        duration: 2,
      })

    gsap.to(qm(".hand"),{
        y: "-40px",
        ease: Power1.easeInOut,
        repeat: -1,
        yoyo: true,
        duration: 2,
        stagger: 1
      })
      gsap.to(qm(".roboface"),{
        y: "-20px",
        ease: Power1.easeInOut,
        repeat: -1,
        yoyo: true,
        duration: 2,
      })

      gsap.to(qm(".imagewrapper"),{
        y: "600px",
        duration: 5,
        scrollTrigger:{
          trigger: ".container.first",
          start: "top top",
          // end: "bottom top",
          toggleActions:"restart pause reverse pause",
          scrub: 1,
        },
      })

    

      
  },[])
  
  

  


  


  return (
    <div className="maincontainer-home" ref={mainref}>
     
      <div className='container first'>
      <div className='backgrnd'>  
      <div className='imagewrapper'>
        <div className='bulb'>
          <img src={lightbulb} alt="bulb" style={{width : "100px"}}/>
        </div>
        <div className='robobody'>
          <img src={robolh} alt="lefthand" className="hand" style={{width : "60px"}}/>          
          <img src={roboface} alt="roboface" className="roboface" style={{width : "100px"}}/>
          <img src={roborh} alt="righthand" className="hand" style={{width : "60px"}}/>


        </div>
      </div>
      
      
      </div>
      <div className='content'>
      Hi, I'm Amith. This website is something that I made out of inspiration and curiosity. I'll be experimenting with the GSAP library and hopefully you'll learn more about me as well as you scroll down.  
      </div>
       <div className='arrowup'>
       <KeyboardArrowUp/>
       </div>         
      </div>
      <div className='container horizontal second' ref={ref}>
      <div className='pages-wrap' >

      <div className='page'>
        Page 1
      </div>

      <div className='page'>
        Page 2
      </div>

      <div className='page'>
        Page 3
      </div>

      </div>
      </div>

      <div className='container third'>
        Div 3
      </div>
      
    
    </div>
  )
}

export default Home


  





